:host {
	// Overrides the home page account menu
	&.home-account-menu {
		button.menu-trigger span {
			color: var(--basic-1) !important;
		}
	}
}

button.menu-trigger {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 8px;

	img.avatar {
		height: 40px;
		width: 40px;
		border-radius: 50%;
		flex-shrink: 0;
		object-fit: cover;
	}

	// User's name
	span {
		max-width: 150px;
	}
}

div.menu {
	width: 290px !important;
	max-width: 290px !important;
	padding: 8px 0;

	a {
		height: 40px;

		mat-icon {
			margin-right: 8px !important;
		}
	}
}
